.partners-content {
  padding-bottom: 64px;

  font-family: $segoe;
  color: $darktext;
}

.partners-content__inner {
  display: flex;
  margin: 0 -8px;
}

.partners-content__col {
  width: 45%;
  flex: 1 1 auto;
  margin: 0 8px;

  &--full {
    width: 100%;
  }

  &--long {
    width: 65%;
  }
}

.partners-content__referrals {
  display: flex;
}

.partners-content__inputs {
  width: 58%;
  flex: 1 1 auto;
}

.partners-content__chart-wrapper {
  width: 42%;
  flex: 1 1 auto;

  display: flex;
  justify-content: flex-end;
}

.partners-content__chart {
  width: 240px;
  height: 160px;
}

.partners-content__block {
  padding: 40px 60px;
  margin-bottom: 16px;

  box-shadow: 0 0 30px rgba(60, 123, 216, 0.1);
  background-color: #ffffff;
}

.partners-content__title {
  margin-bottom: 35px;

  font-size: 16px;
  font-weight: 700;

  &--small-margin {
    margin-bottom: 25px;
  }
}

.partners-content__status {
  display: inline-block;
  margin-left: 8px;
  padding: 2px 20px 3px;

  font-size: 14px;
  font-weight: 700;
  color: #fff;

  border-radius: 20px;

  &--active {
    background-color: #5fce67;
  }
}

.partners-content__info {
  display: flex;
  flex-flow: row nowrap;
  margin-bottom: 20px;
}

.partners-content__info-col {
  flex: 1 1 auto;

  &:first-child {
    margin-right: 20px;
  }
}

.partners-content__info-value {
  font-size: 24px;
  font-weight: 700;
  line-height: 1;

  &:first-child {
    margin-bottom: 10px;
  }
}

.partners-content__info-label {
  font-size: 14px;
  line-height: 1;

  &:first-child {
    margin-bottom: 10px;
  }
}

.partners-content__info-tip {
  padding-top: 20px;
  margin: 0;

  font-size: 14px;
  line-height: 1;

  border-top: 1px solid #ebebeb;

  b {
    font-size: 16px;
    font-weight: 700;
  }
}

.partners-content__input {
  position: relative;
  width: 100%;
  margin-bottom: 35px;

  .base-input__input {
    padding-right: 40px;
  }
}

.partners-content__copy {
  position: absolute;
  width: 15px;
  height: 15px;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);

  background: none;
  border: none;

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    border: 1px solid $themecolor;

    background-color: #fff;
  }

  &::before {
    top: -2px;
    right: 5px;
  }

  &::after {
    right: 0;
    top: 3px;
  }

  &:hover,
  &:active,
  &:focus {
    outline: none;
  }

  &.clicked {
    animation: bounce 500ms ease;
  }
}

.partners-content__table {
  width: 100%;
  border-collapse: collapse;
  margin-left: -32px;
  table-layout: fixed;
}

.partners-content__table-row {
  width: 100%;

  &:nth-child(2n) {
    background-color: rgba(#f5f7f9, 0.5);
  }

  td {
    width: 10%;

    &:first-child {
      width: 25%;
    }
  }

  th {
    &:first-child {
      width: 25%;
    }
  }

  &.table-footer {
    background-color: #fff;

    td {
      padding-top: 24px;

      font-weight: 700;
    }
  }

  &.table-header {
    th {
      font-size: 12px;
      text-align: left;
    }
  }
}

.partners-content__cell {
  padding: 12px 32px;

  font-size: 16px;
  font-weight: 400;
}

.partners-content__add-link {
  @include btn-theme;
  position: relative;
  margin: 32px 0 40px;
  padding: 16px 40px 16px 80px;

  font-size: 18px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 32px;
    height: 1px;
    left: 32px;
    top: 50%;

    background-color: #fff;
  }

  &::before {
    transform: rotate(90deg);
  }
}

.partners-content__recommendation {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.partners-content__recommendation-submit {
  @include btn-theme;
  position: relative;
  margin: 32px 0 40px;
  padding: 16px 40px 16px;

  font-size: 18px;
}

.partners-content__switchers {
  width: 40%;
  padding-left: 50px;
}

.partners-content__switcher {
  position: relative;
  margin: 50px 0;
}

.partners-content__switcher-input {
  position: absolute;
  opacity: 0;
}

.partners-content__switcher-label {
  position: relative;
  display: block;

  cursor: pointer;
}

.partners-content__switcher-icon {
  position: absolute;
  width: 32px;
  height: 16px;
  left: 0;
  top: 50%;
  transform: translateY(-35%);
  border-radius: 8px;
  background-color: #f5f7f9;

  transition: background-color 300ms ease;

  &::before {
    content: "";
    position: absolute;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    left: 1px;
    top: 1px;
    border: 1px solid white;

    background-color: #bdc6d0;

    transition: transform 300ms ease, background-color 300ms ease;
  }
}

.partners-content__switcher-input:checked + .partners-content__switcher-icon {
  background-color: #5fce67;

  &::before {
    background-color: #fff;
    left: auto;
    transform: translateX(17px);
  }
}

.partners-content__switcher-text {
  display: block;
  padding-left: 47px;

  font-size: 16px;
}

@media (max-width: 1280px) {
  .partners-content {
    padding-bottom: 34px;
  }

  .partners-content__inner {
    display: block;
    margin: 0;
  }

  .partners-content__col {
    width: 100%;
    margin: 0 0 16px;
  }
}

@media (max-width: 768px) {
  .partners-content__table-wrapper {
    overflow-x: auto;
  }

  .partners-content__table {
    width: 800px;
  }

  .partners-content__referrals {
    display: block;
  }

  .partners-content__inputs {
    width: 100%;
    margin: 0 0 25px;
  }

  .partners-content__chart-wrapper {
    width: 100%;
    justify-content: flex-start;
  }

  .partners-content__recommendation {
    display: block;
  }

  .partners-content__switchers {
    padding-left: 0;
    width: 100%;
  }

  .partners-content__switcher {
    margin: 25px 0;
  }
}

@media (max-width: 480px) {
  .partners-content__block {
    padding: 25px 15px;
  }

  .partners-content__title {
    margin-bottom: 25px;

    &--small-margin {
      margin-bottom: 15px;
    }
  }

  .partners-content__input {
    margin-bottom: 30px;
  }

  .partners-content__info {
    display: block;
  }

  .partners-content__info-col {
    width: 100%;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .partners-content__add-link {
    margin: 20px 0 25px;
    padding: 14px 30px 14px 50px;
    font-size: 16px;

    &::before,
    &::after {
      width: 20px;
      left: 20px;
    }
  }
}

@keyframes bounce {
  0% {
    transform: translateY(-50%);
  }
  50% {
    transform: translateY(-100%);
  }
  100% {
    ransform: translateY(-50%);
  }
}
