.monitoring-finances {
  padding: 40px 30px 30px 40px;
  margin-bottom: 16px;

  box-shadow: 0 0 30px rgba(60, 123, 216, 0.1);
}

.monitoring-finances__select {
  margin-bottom: 24px;
}

.monitoring-finances__title {
  margin: 0;
  margin-bottom: 30px;

  font-size: 16px;
  font-weight: 700;
}

.monitoring-finances__links-row {
  display: inline-flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.monitoring-finances__chart-link {
  margin-right: 10px;
  margin-bottom: 10px;
  display: block;
  position: relative;
  padding: 10px 22px;
  min-width: 72px;

  color: $darktext;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
  text-align: center;
  text-decoration: none;

  border-radius: 4px;
  border: 1px solid #3c7bd8;
  background-color: $lighttext;

  cursor: pointer;

  transition: color 300ms ease, background-color 300ms ease;

  &:hover,
  &:active,
  &:focus,
  &.active {
    background-color: #3c7bd8;
    color: $lighttext;
    text-decoration: none;
  }
}

.monitoring-finances__graph {
  //   padding-right: 70px;
  width: 700px;
  margin-bottom: 35px;
}

.monitoring-finances__timeline {
  width: 700px;
  height: 300px;
  margin-bottom: 40px;

  #timelinechart {
    position: relative;
    width: 100%;
    height: 100%;

    .simplebar-track {
      width: 100%;

      background-color: rgba(233, 233, 233, 0.582);

      &.vertical {
        display: none;
      }
    }

    .simplebar-scrollbar {
      width: 4px;
      right: 0;

      &::before {
        opacity: 1;
        background-color: $themecolor;
      }
    }

    .simplebar-track.horizontal {
      height: 7px;
      border-radius: 4px;
    }

    .horizontal.simplebar-track .simplebar-scrollbar {
      height: 7px;
      border-radius: 4px;
      top: 0;
    }
  }
}

.monitoring-finances__graph-chart {
  height: 200px;
  margin-bottom: 30px;

  #income-monitoring {
    width: 100%;
    height: 100%;

    .simplebar-track {
      width: 100%;

      background-color: rgba(233, 233, 233, 0.582);

      &.vertical {
        display: none;
      }
    }

    .simplebar-scrollbar {
      width: 4px;
      right: 0;

      &::before {
        opacity: 1;
        background-color: $themecolor;
      }
    }

    .simplebar-track.horizontal {
      height: 7px;
      border-radius: 4px;
    }

    .horizontal.simplebar-track .simplebar-scrollbar {
      height: 7px;
      border-radius: 4px;
      top: 0;
    }
  }
}

.monitoring-finances__legend {
  display: flex;
  flex-wrap: wrap;
}

.monitoring-finances__legend-item {
  position: relative;
  padding-left: 30px;
  margin-right: 35px;
  margin-bottom: 15px;
  font-size: 14px;

  &::before {
    content: "";
    position: absolute;
    width: 18px;
    height: 18px;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }

  &--blue::before {
    background-color: #3c7bd8;
  }

  &--yellow::before {
    background-color: #ffd729;
  }
}

.monitoring-finances__data {
  display: flex;
  margin-bottom: 35px;
}

.monitoring-finances__data-col {
  flex: 0 1 auto;
  margin-right: 10%;

  &:last-child {
    margin-right: 0;
  }
}

.monitoring-finances__data-title {
  margin-bottom: 3px;

  font-size: 14px;
  font-weight: 400;
}

.monitoring-finances__data-text {
  font-size: 16px;
  font-weight: 700;
}

.monitoring-finances__info {
  margin: 0;

  font-size: 14px;
  line-height: 1.5;
}

@media (max-width: 1280px) {
  .monitoring-finances__timeline,
  .monitoring-finances__graph {
    width: 650px;
  }
}

@media (max-width: 1024px) {
  .monitoring-finances__timeline,
  .monitoring-finances__graph {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .monitoring-finances {
    padding: 20px 15px;
  }

  .monitoring-finances__tip {
    margin-bottom: 30px;
  }

  .monitoring-finances__graph {
    padding-right: 0;
  }

  .monitoring-finances__graph-chart {
    margin-bottom: 15px;
  }

  .monitoring-finances__data {
    flex-wrap: wrap;
    margin-bottom: 15px;
  }

  .monitoring-finances__data-col {
    flex-shrink: 0;
    margin-right: 5%;
    margin-bottom: 15px;

    &:last-child {
      margin-right: 0;
    }
  }
  .monitoring-finances__chart-link {
    min-width: 45px;
    padding: 7px 12px;
  }
}
